.inputContainer {
    
}

.input {
  font-family: "Inter", Arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  width: 295px;
  padding: 12px 24px;
  align-items: center;
  border: none;
  border-radius: 10px;
  background: #fff;
  box-sizing: border-box;
  transition: all 0.2s ease-in-out;
}

.input:focus {
  outline: none;
  box-shadow: 0px 1px 1px 0px rgba(202, 200, 200, 0.985),
    0px 1px 1px 0px rgba(0, 0, 0, 0.25);
}

.input::placeholder {
  color: #b5b5b5;
}
